import { forwardRef, useContext } from "react";

import { InputProps } from "./types";
import InputStyledComponents from "./styled";

import DropdownContext from "../context";

import DownSVG from "./DownSVG";

const { Container } = InputStyledComponents;

const Input = forwardRef<HTMLDivElement, InputProps>(
  ({ value, isSelected, isBlock, isDisabled, onClick }, ref) => {
    const { size } = useContext(DropdownContext);

    return (
      <Container
        ref={ref}
        onClick={(e) => {
          if (!isDisabled) {
            e.stopPropagation();
            if (onClick) {
              onClick(e);
            }
          }
        }}
        size={size}
        isSelected={!!isSelected}
        isBlock={!!isBlock}
        isDisabled={!!isDisabled}
      >
        <span>{value}</span>
        <DownSVG />
      </Container>
    );
  }
);

export default Input;
