import {
  FC,
  useEffect,
  useRef,
  Fragment,
  cloneElement,
  useContext,
} from "react";

import { DialogDropdownProps } from "../types";
import { DialogContext } from "../context";

const Dropdown: FC<DialogDropdownProps> = ({ children, margin = 0 }) => {
  const { triggerRect, triggerPosition } = useContext(DialogContext);
  const ref = useRef<HTMLElement>();

  useEffect(() => {
    const current = ref.current;
    if (current && triggerRect && triggerPosition) {
      const top = triggerRect.top + triggerRect.height + margin;

      const isOverY = top + current.offsetHeight > window.innerHeight;
      if (isOverY) {
        current.style.position = "absolute";
        current.style.width = `${triggerRect.width}px`;
        current.style.top = `${
          triggerPosition.top - current.offsetHeight - margin
        }px`;
        current.style.left = `${triggerPosition.left}px`;
      } else {
        current.style.position = "absolute";
        current.style.width = `${triggerRect.width}px`;
        current.style.top = `${
          triggerPosition.top + triggerRect.height + margin
        }px`;
        current.style.left = `${triggerPosition.left}px`;
      }
    }
  }, [triggerRect, triggerPosition, margin]);

  return (
    <Fragment>
      {cloneElement(children, {
        ...children.props,
        ref,
      })}
    </Fragment>
  );
};

export default Dropdown;
