import { FC, useMemo, useState } from "react";

import { InputProps } from "./types";
import InputStyledComponents from "./styled";
import CloseSVG from "./CloseSVG";
import AttentionSVG from "./AttentionSVG";

const { Container, InputContainer, ErrorMessage } = InputStyledComponents;

const Input: FC<InputProps> = ({
  type = "box",
  size = "medium",
  value = "",
  errorMessage,
  onChange,
  ...props
}) => {
  const [isFocused, setIsFocused] = useState(false);

  const handleFocus = () => {
    setIsFocused(true);
  };

  const handleBlur = () => {
    setTimeout(() => {
      setIsFocused(false);
    }, 200);
  };

  const handleChange = (value: string) => {
    if (onChange) {
      onChange(value);
    }
  };

  const handleClearClick = () => {
    if (onChange) {
      onChange("");
    }
  };

  const isTyping = useMemo(() => {
    return !!value && isFocused;
  }, [value, isFocused]);

  const isCompleted = useMemo(() => {
    return !!value && !isFocused;
  }, [value, isFocused]);

  return (
    <Container>
      <InputContainer
        type={type}
        size={size}
        isFocused={isFocused}
        isTyping={isTyping}
        isCompleted={isCompleted}
        hasErrorMessage={!!errorMessage}
      >
        <input
          type="text"
          onFocus={handleFocus}
          onBlur={handleBlur}
          value={value}
          onChange={({ target: { value } }) => handleChange(value)}
          {...props}
        />
        {!errorMessage && isTyping && (
          <span onClick={handleClearClick}>
            <CloseSVG />
          </span>
        )}
        {!!errorMessage && <AttentionSVG />}
      </InputContainer>
      {!!errorMessage && <ErrorMessage>{errorMessage}</ErrorMessage>}
    </Container>
  );
};

export default Input;
