import { FC } from "react";

import { TagProps } from "./types";
import TagStyledComponents from "./styled";

const { Container } = TagStyledComponents;

const Tag: FC<TagProps> = ({ children, isFilled, isCapsule }) => {
  return (
    <Container isFilled={!!isFilled} isCapsule={!!isCapsule}>
      {children}
    </Container>
  );
};

export default Tag;
