import { cloneElement, FC, useContext, useEffect } from "react";
import { DialogTriggerProps } from "./types";
import { DialogContext, DIALOG_DATA_ATTRIBUTE_NAME } from "./context";

const Trigger: FC<DialogTriggerProps> = ({ children, disabled, action }) => {
  const { id, isOpen, handleOpen, handleClose, handleWindowClose } =
    useContext(DialogContext);

  // Window 클릭 시 닫히게 하는 effect
  useEffect(() => {
    window.addEventListener("click", handleWindowClose);
    return () => {
      window.removeEventListener("click", handleWindowClose);
    };
  }, [action, handleWindowClose, id]);

  const injectAction = () => {
    switch (action) {
      case "custom": {
        return {};
      }
      case "hover": {
        return {
          onMouseEnter: (e: React.MouseEvent) => {
            if (!disabled && !isOpen) {
              handleOpen(e);
            }
            if (children.props.onMouseEnter) {
              children.props.onMouseEnter(e);
            }
          },
          onMouseLeave: (e: React.MouseEvent) => {
            handleClose();
            if (children.props.onMouseLeave) {
              children.props.onMouseLeave(e);
            }
          },
        };
      }
      case "click":
      default: {
        return {
          onClick: (e: React.MouseEvent) => {
            if (!disabled) {
              handleOpen(e);
            }

            if (children.props.onClick) {
              children.props.onClick(e);
            }
          },
        };
      }
    }
  };

  return cloneElement(children, {
    [DIALOG_DATA_ATTRIBUTE_NAME]: id,
    ...injectAction(),
  });
};

export default Trigger;
