import { FC } from "react";

import { ButtonProps } from "./types";
import { ButtonStyledComponent } from "./styled";

const { StyledButton } = ButtonStyledComponent;

const Button: FC<ButtonProps> = ({ icon, children, isDisabled, ...props }) => {
  const renderChildren = () => {
    const items = [];
    if (icon) {
      if (icon.position === "leading") {
        items.push(
          <span key={0}>{icon.node}</span>,
          <span key={1}>{children}</span>
        );
      } else if (icon.position === "trailing") {
        items.push(
          <span key={0}>{children}</span>,
          <span key={1}>{icon.node}</span>
        );
      }
    } else {
      items.push(children);
    }

    return items;
  };

  return (
    <StyledButton disabled={isDisabled} {...props}>
      {renderChildren()}
    </StyledButton>
  );
};

export default Button;
