import { forwardRef, useContext } from "react";

import { ListProps } from "./types";
import { DropdownOption } from "../types";
import ListStyledComponents from "./styled";

import DropdownContext from "../context";

const { Container } = ListStyledComponents;

const List = forwardRef<HTMLUListElement, ListProps>(
  ({ options, onItemClick }, ref) => {
    const { size } = useContext(DropdownContext);
    const handleClick = (value: DropdownOption["value"]) => {
      if (onItemClick) {
        onItemClick(value);
      }
    };
    return (
      <Container ref={ref} size={size} onClick={(e) => e.stopPropagation()}>
        {options.map(({ value, title }) => {
          return (
            <li key={value} onClick={() => handleClick(value)}>
              {title || value}
            </li>
          );
        })}
      </Container>
    );
  }
);

export default List;
