import { FC } from "react";

import { CheckboxProps } from "./types";
import CheckboxStyledComponents from "./styled";

import CheckSVG from "./CheckSVG";

const { Container, CheckArea } = CheckboxStyledComponents;

const Checkbox: FC<CheckboxProps> = ({
  isChecked,
  isDisabled,
  onChange,
  label,
  size = "small",
  ...props
}) => {
  const handleClick = () => {
    if (!isDisabled) {
      if (onChange) {
        onChange(!isChecked);
      }
    }
  };

  return (
    <Container size={size} isDisabled={isDisabled}>
      <input
        type="checkbox"
        checked={isChecked}
        disabled={isDisabled}
        hidden
        onChange={({ target: { checked } }) => {
          if (onChange) {
            onChange(checked);
          }
        }}
        {...props}
      />
      <CheckArea
        size={size}
        isChecked={!!isChecked}
        isDisabled={isDisabled}
        onClick={handleClick}
      >
        {isChecked && <CheckSVG />}
      </CheckArea>
      <span>{label}</span>
    </Container>
  );
};

export default Checkbox;
