import { FC } from "react";

import { RadioButtonProps } from "./types";
import RadioButtonStyledComponents from "./styled";

import RoundSVG from "./RoundSVG";

const { CheckArea, Container } = RadioButtonStyledComponents;

const RadioButton: FC<RadioButtonProps> = ({
  isChecked,
  isDisabled,
  label,
  size = "small",
  onChange,
  ...props
}) => {
  const handleClick = () => {
    if (!isDisabled) {
      if (onChange) {
        onChange(true);
      }
    }
  };

  return (
    <Container size={size}>
      <input
        type="radio"
        checked={isChecked}
        disabled={isDisabled}
        hidden
        onChange={({ target: { checked } }) => {
          if (onChange) {
            onChange(checked);
          }
        }}
        {...props}
      />
      <CheckArea
        size={size}
        isChecked={!!isChecked}
        isDisabled={!!isDisabled}
        onClick={handleClick}
      >
        {isChecked && <RoundSVG />}
      </CheckArea>
      <span>{label}</span>
    </Container>
  );
};

export default RadioButton;
