import { FC } from "react";

import { PaginationProps } from "./types";
import PaginationStyledComponents from "./styled";

const { DotContainer, NumberContainer, Dot } = PaginationStyledComponents;

const Pagination: FC<PaginationProps> = ({
  type = "number",
  color = "gray01",
  total = 1,
  current = 1,
  onChange,
}) => {
  switch (type) {
    case "dot":
      return (
        <DotContainer>
          {Array.from({ length: total }).map((_, index) => {
            const isActive = index + 1 === current;

            return (
              <Dot
                key={index}
                isActive={isActive}
                onClick={() => {
                  if (onChange) {
                    onChange(index + 1);
                  }
                }}
              />
            );
          })}
        </DotContainer>
      );
    case "number":
    default:
      return (
        <NumberContainer color={color}>
          {current}/{total}
        </NumberContainer>
      );
  }
};

export default Pagination;
