import { FC } from "react";

import { ChipProps } from "./types";
import ChipStyledComponents from "./styled";

import CheckSVG from "./CheckSVG";

const { Container } = ChipStyledComponents;

const Chip: FC<ChipProps> = ({
  isSelected,
  isDisabled,
  size,
  hasTrailingIcon,
  children,
  onChange,
  ...props
}) => {
  const handleClick = () => {
    if (!isDisabled) {
      if (onChange) {
        onChange(!isSelected);
      }
    }
  };

  return (
    <Container
      isSelected={isSelected}
      isDisabled={isDisabled}
      size={size}
      onClick={handleClick}
    >
      <input
        type="checkbox"
        checked={isSelected}
        disabled={isDisabled}
        hidden
        onChange={({ target: { checked } }) => {
          if (onChange) {
            onChange(checked);
          }
        }}
        {...props}
      />
      {children}
      {hasTrailingIcon && isSelected && <CheckSVG />}
    </Container>
  );
};

export default Chip;
