import styled from "@emotion/styled";
import { css } from "@emotion/react";

import { StyledButtonProps } from "./types";

import { getColor, typography } from "../../../foundations";

const TextButtonStyledComponents = {
  StyledButton: styled.button<StyledButtonProps>`
    display: flex;
    align-items: center;
    padding: 0 8px;
    background: none;
    border: none;
    cursor: pointer;

    &:hover {
      text-decoration: underline;
      text-underline-offset: 5px;
    }

    &:disabled {
      ${getColor("color", "gray200")};
      cursor: default;

      &:hover {
        text-decoration: none;
      }
    }

    ${({ size }) => {
      switch (size) {
        case "small": {
          return css`
            ${typography.body03M};
          `.styles;
        }
        case "large": {
          return css`
            ${typography.body01M};
          `.styles;
        }
        case "medium":
        default: {
          return css`
            ${typography.body02M};
          `.styles;
        }
      }
    }}
  `,
};

export default TextButtonStyledComponents;
