import { ChartDataType } from "../types";

import { NumberUtility } from "../../../base/util";

const { getRandomNumber } = NumberUtility;

export const getRandomNumberData = (options?: {
  boundary?: [number, number];
}) => {
  if (options?.boundary) {
    return getRandomNumber(options.boundary[0], options.boundary[1]);
  } else {
    const min = 1000 * 1000;
    const max = 10 * 1000 * 1000;

    return getRandomNumber(min, max);
  }
};

/**
 *
 * @param value 정수만 허용
 * @returns
 */
export const getKoreanUnitNumber = (value: number) => {
  const stringValue = value.toFixed();
  switch (stringValue.length) {
    case 1:
    case 2:
    case 3:
    case 4:
      return value.toLocaleString();
    case 5:
    case 6:
    case 7:
    case 8:
      return `${(value / (1000 * 10)).toFixed(0)}만`;
    case 9:
    default:
      return `${(value / (1000 * 1000 * 100)).toFixed(0)}억`;
  }
};

export const getValueByScaleType = (
  type: ChartDataType,
  value: number | string | Date
) => {
  switch (type) {
    case "number": {
      return getKoreanUnitNumber(Number(value));
    }
    case "year": {
      console.log(value);
      return value;
    }
  }
};

const getDateNumberString = (num: number) => {
  if (num < 10) {
    return `0${num}`;
  } else {
    return `${num}`;
  }
};

export const getRandomDate = () => {
  return `${getRandomNumber(2012, 2023)}-${getDateNumberString(
    getRandomNumber(1, 12)
  )}-${getDateNumberString(getRandomNumber(1, 28))}`;
};
