import { useMemo } from "react";

import { DropdownComponent, DropdownOption } from "./types";
import DropdownStyledComponents from "./styled";

import DropdownContext from "./context";

import Input from "./Input";
import List from "./List";

import Dialog from "../../../base/Dialog";

const { Container, StyledSelect } = DropdownStyledComponents;

const Dropdown: DropdownComponent = ({
  size = "medium",
  options,
  selectedValue,
  onChange,
  isBlock,
  isDisabled,
  ...props
}) => {
  const selectedOption = useMemo(
    () => options.find(({ value }) => value === selectedValue) || options[0],
    [options, selectedValue]
  );

  const handleChange = (value: DropdownOption["value"]) => {
    if (onChange) {
      onChange(value);
    }
  };

  return (
    <DropdownContext.Provider value={{ size }}>
      <Container>
        <StyledSelect
          value={selectedValue}
          onChange={({ target: { value } }) => handleChange(value)}
          disabled={isDisabled}
          hidden
          {...props}
        >
          {options.map((option) => {
            return (
              <option key={option.value} value={option.value}>
                {option.title || option.value}
              </option>
            );
          })}
        </StyledSelect>
        <Dialog>
          {({ isOpen }) => {
            return (
              <>
                <Dialog.Trigger>
                  <Input
                    isBlock={isBlock}
                    isDisabled={isDisabled}
                    isSelected={isOpen}
                    value={selectedOption?.title}
                  />
                </Dialog.Trigger>
                <Dialog.Body>
                  <Dialog.Body.Dropdown margin={4}>
                    <List options={options} onItemClick={handleChange} />
                  </Dialog.Body.Dropdown>
                </Dialog.Body>
              </>
            );
          }}
        </Dialog>
      </Container>
    </DropdownContext.Provider>
  );
};

Dropdown.Input = Input;
Dropdown.List = List;

export default Dropdown;
