import React from "react";
import ReactDOM from "react-dom/client";
import App from "./App";
import { getDomains } from "./utils/network";
import {
  COMPONENT_DOMAIN_ID,
  DATA_VISUALIZATION_DOMAIN_ID,
  getDomain,
} from "./utils/domain";

const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);
getDomains().then((domains) => {
  const componentDomain = getDomain(COMPONENT_DOMAIN_ID, domains.reverse());
  const dataVisualizationDomain = getDomain(
    DATA_VISUALIZATION_DOMAIN_ID,
    domains.reverse()
  );
  if (componentDomain && dataVisualizationDomain) {
    root.render(
      <App
        domains={
          [...componentDomain.children, ...dataVisualizationDomain.children] ||
          []
        }
      />
    );
  }
});
