import { FC, cloneElement, useEffect, useMemo, useState } from "react";

import { PlaygroundProps } from "./types";
import PlaygroundStyledComponent from "./styled";

const { Container, Body, ComponentArea, Title, ControlArea } =
  PlaygroundStyledComponent;

const Playground: FC<PlaygroundProps> = ({ component, controls }) => {
  const [props, setProps] = useState(component.element.props);
  useEffect(() => {
    setProps(component.element.props);
  }, [component.element.props]);

  const memo = useMemo(() => {
    return cloneElement(component.element, props);
  }, [component, props]);

  const renderInput = () => {
    if (controls) {
      return controls?.map((control, index) => {
        switch (control.type) {
          case "text":
            return (
              <div key={index}>
                <div>{control.propName}</div>
                <input
                  value={props[control.propName] || ""}
                  onChange={({ target: { value } }) => {
                    setProps({
                      ...props,
                      [control.propName]: value,
                    });
                  }}
                />
              </div>
            );
          case "radio":
            return (
              <div key={index}>
                <div>{control.propName}</div>
                {control.config.options.map((option, index) => {
                  return (
                    <div key={index} style={{ whiteSpace: "nowrap" }}>
                      <input
                        type="radio"
                        checked={props[control.propName] === option.value}
                        onChange={() => {
                          setProps({
                            ...props,
                            [control.propName]: option.value,
                          });
                        }}
                      />
                      <span>{option?.title || option.value}</span>
                    </div>
                  );
                })}
              </div>
            );
          default:
            return null;
        }
      });
    }

    return null;
  };

  return (
    <Container>
      <Title>{component.title}</Title>
      <Body>
        <ComponentArea>{memo}</ComponentArea>
        <ControlArea>{renderInput()}</ControlArea>
      </Body>
    </Container>
  );
};

export default Playground;
