import { FC, useEffect, useRef, useState } from "react";

import { TabItem, TabProps } from "./types";
import TabStyledComponents from "./styled";

const { Container, Indicator, TabList, TabListItem } = TabStyledComponents;

const Tab: FC<TabProps> = ({
  type = "text",
  size = "medium",
  items,
  onChange,
  selectedItemKey = items[0].key,
}) => {
  const containerRef = useRef<HTMLDivElement>(null);
  const liRef = useRef<HTMLLIElement>(null);

  const handleClick = (item: TabItem) => {
    if (onChange) {
      onChange(item);
    }
  };

  const [liWidth, setLiWidth] = useState(0);
  const [liLeft, setLiLeft] = useState(0);

  useEffect(() => {
    if (liRef.current) {
      const target = liRef.current;

      setLiWidth(target.offsetWidth);
      setLiLeft(target.offsetLeft);
    }
  }, [selectedItemKey, size, type]);

  return (
    <Container ref={containerRef} theme={{ type, size }}>
      <Indicator
        key={selectedItemKey}
        theme={{ type, size }}
        style={{
          width: type === "text" ? liWidth - 32 : liWidth,
          left: type === "text" ? liLeft + 16 : liLeft,
        }}
      />
      <TabList theme={{ type, size }}>
        {items.map((item) => {
          const { key, title } = item;

          return (
            <TabListItem
              key={key}
              theme={{ type, size, isActive: key === selectedItemKey }}
              ref={key === selectedItemKey ? liRef : undefined}
              onClick={() => handleClick(item)}
            >
              {title}
            </TabListItem>
          );
        })}
      </TabList>
    </Container>
  );
};

export default Tab;
