import { ChartArea } from "chart.js";

import { getRandomNumberData } from "./number";

export const regions = ["구로구", "강서구", "노원구", "서초구", "광진구"];

export const years = Array.from({ length: 12 }).map((_, index) => index + 2012);

export function getGradient(
  ctx: CanvasRenderingContext2D,
  chartArea: ChartArea,
  color: string
) {
  let width: number = 0,
    height: number = 0,
    gradient: CanvasGradient | undefined = undefined;

  const chartWidth = chartArea.right - chartArea.left;
  const chartHeight = chartArea.bottom - chartArea.top;
  if (!gradient || width !== chartWidth || height !== chartHeight) {
    // Create the gradient because this is either the first render
    // or the size of the chart has changed
    width = chartWidth;
    height = chartHeight;
    gradient = ctx.createLinearGradient(0, chartArea.bottom, 0, chartArea.top);
    const rgb = hexToRgb(color);
    if (rgb) {
      const { r, g, b } = rgb;
      gradient.addColorStop(0, `rgba(${r}, ${g}, ${b}, 0)`);
      gradient.addColorStop(0.2, `rgba(${r}, ${g}, ${b}, 0.1)`);
      gradient.addColorStop(0.8, `rgba(${r}, ${g}, ${b}, 0.9)`);
      gradient.addColorStop(1, `rgba(${r}, ${g}, ${b}, 1)`);
    }
  }

  return gradient;
}

export const regionDivergingChartData = regions.map((_, index) => {
  return getRandomNumberData({
    boundary: index % 2 ? [100, 500] : [-500, -100],
  });
});

export const yearDivergingChartData = years.map((_, index) => {
  return getRandomNumberData({
    boundary: index % 2 ? [100, 500] : [-500, -100],
  });
});

export const segmentLineChartData = years.map((year) =>
  year ? getRandomNumberData() : null
);

export function hexToRgb(hex: string) {
  var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
  return result
    ? {
        r: parseInt(result[1], 16),
        g: parseInt(result[2], 16),
        b: parseInt(result[3], 16),
      }
    : null;
}
