import { FC } from "react";

import { TextButtonProps } from "./types";
import TextButtonStyledComponents from "./styled";

const { StyledButton } = TextButtonStyledComponents;

const TextButton: FC<TextButtonProps> = ({ icon, children, isDisabled, ...props }) => {
  const renderChildren = () => {
    const items = [];
    if (icon) {
      if (icon.position === "trailing") {
        items.push(
          <span key={0}>{children}</span>,
          <span key={1}>{icon.node}</span>
        );
      }
    } else {
      items.push(children);
    }
    return items;
  };
  return <StyledButton disabled={isDisabled} {...props}>{renderChildren()}</StyledButton>;
};

export default TextButton;